body {
  margin: 1;
  /* text-align: center; */
  font-family: 'monoSpace';
  font-size: large;
  padding-left: 20px;
  color: lightgray;

  background-color: #202020;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.loadingMessage {
  color: #B04545;
}

.playerName:hover {
  text-shadow: 0 0 10px #FFFFFF;
  color: #FFFFFF;
  font-style: bold;
}

.winnerPlayerName {
  color:#459956
}

.loserPlayerName {
  color:#B04545
}

.winnerPlayerName:hover {
  text-shadow: 0 0 10px #FFFFFF;
  color: #FFFFFF;
  font-style: bold;
}

.loserPlayerName:hover {
  text-shadow: 0 0 10px #FFFFFF;
  color: #FFFFFF;
  font-style: bold;
}

.machineName:hover {
  color: #FFFFFF;
  text-shadow: 0 0 10px #A0A0FF;
  font-style: bold;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
